export const myuzbbir = [`O'zbekiston iqtisodiyoti bozor iqtisodiyotiga bosqichma-bosqich o'tadi, tashqi savdo siyosati import o'rnini bosishga asoslangan. 2017-yil sentabridan boshlab mamlakat valyutasi bozor kursi bo'yicha to'liq konvertatsiya qilinmoqda. O'zbekiston paxta tolasini ishlab chiqaruvchi va eksport qiluvchi yirik ta'minotchi davlatdir. Mamlakatda, shuningdek, dunyodagi eng yirik oltin konlari mavjud. Sovet davridagi ulkan energiya ishlab chiqarish qurilmalari va tabiiy gazni yetkazib berish bilan O'zbekiston Markaziy Osiyodagi eng yirik elektr ishlab chiqaruvchisi bo'ldi.`,
    `Bugungi kunda O'zbekiston nomi bilan tanilgan hudud oldin Movarounnahr, So'g'diyona hamda Buxoro xonligi nomlari bilan ham tanilgan. XIV asrda ushbu mintaqada Amir Temur dunyoga kelgan va o'z saltanatining markaziy qismiga aylantirgan. Amir Temur davrida ushbu hudud Qora dengizdan Arabiston dengiziga va Hindistonning Dehli shahriga qadar cho'zilgan imperiyaning poytaxtiga aylangan.`,
    `Sharqiy Osiyo va G'arb orasida savdo aloqalari rivojlanar ekan, Movarounnahr hududidagi shaharlar va qishloqlar ushbu savdo aloqalarida muhim ahamiyat kasb eta boshladi va ushbu savdo munosabatlarida badavlat so'g'd savdogarlarining roli katta bo'lgan. Keyinchalik Buyuk Ipak yo'li nomi bilan tanilgan ushbu savdo yo'lida joylashgan Buxoro va Samarqand kabi shaharlar oxir-oqibatda o'ta badavlat shaharlarga aylandi va Movarounnahrning o'zi esa Antik davrning eng ta'sirli va qudratli hududlaridan biriga aylandi`,
    `Temur o'limidan keyin Temuriylar saltanati tezda ikkiga bo'linadi. Temuriylarning tinimsiz o'zaro urushlari Oroldan shimolda yashovchi o'zbek ko'chmanchi qabilalarining e'tiborini tortadi va 1501-yilda o'zbek qo'shinlari Movarounnahrni bosib olishni boshlaydi.[17] Aynan shu davrda Buxoro xonligida qullar savdosi boshlanadi va keyinchalik mustahkamlanadi. Keyinchalik qisqa muddat Buxoro xonligi Nodirshoh boshchiligidagi Fors imperiyasi tomonidan egallanadi va bir muddat o'sha davrdagi Fors imperiyasi tarkibiga kiritiladi. Rossiya Imperiyasining bosqini davrida Movarounnahr Buxoro amirligi, Xiva va Qo'qon xonliklari orasida bo'lib olingandi.`,
    `1990-yil 20-iyunda O'zbekiston SSR Mustaqillik deklaratsiyasi e'lon qilindi. Moskvadagi avgust davlat to'ntarishi barbod bo'lganidan keyin 1991-yil 31-avgustda O'zbekiston Respublikasining davlat mustaqilligi e'lon qilindi.1-sentyabr Mustaqillik kuni deb belgilandi. Shu yilning 26-dekabr kunida Sovet Ittifoqi rasman tugatilgani e'lon qilindi. 1989-yildan beri O'zbekiston Kommunistik Partiyasining birinchi kotibi Islom Karimov 1990-yilda O'zbekiston SSR Prezidenti etib saylandi. 1991-yildagi Sovet Ittifoqi parchalanishidan keyin Karimov mustaqil O'zbekiston Prezidenti etib saylandi. Karimov davri O'zbekiston tarixida o'chmas iz qoldirdi va mutaxassislar fikricha Karimov davrida O'zbekistonda avtoritar tuzum shakllangan.`,
    `Mavjud bo'lgan mineral xom-ashyo zaxiralarining aksariyati hozir ishlab turgan kon qazish komplyekslarini istiqboldagi uzoq muddat mobaynida xom-ashyo bilan ta'minlaydi, ayni vaqtda, oltin, uran mis, qo'rg'oshin, kumush, litiy, fosforitlar, kaliy tuzlari, dala shpati, vollastonit, agrokimyo ma'danlari va shu kabi juda muhim foydali qazilmalarni qazib olishni tashkil etish, quvvatlarni ko'paytirishga imkon beradi.`,
    `Taxminan 780 yilda olim Muhammad al-Xorazmiy Xorazmda yoki hozirgi Xorazm viloyatida tug'ilgan. U matematika, astronomiya va geografiyaga katta hissa qo'shgan. Algebra so'zi uning birinchi va ikkinchi darajali tenglamalarning yechimiga bag'ishlangan Kitob al-jabr va-l-muqobala kitobi nomidan olingan. Algoritm so'zi uning nomining lotincha transkripsiyasidan boshqa narsa emas.`,
    `Zebunniso begim - shoira, ma'rifatparvar. Boburiylar sulolasidan Avrangzebning qizi. Onasi Dilrasbonuning shajarasi Safaviylarga va Boburning qizi - Gulbadan begimga borib taqaladi.Maxfiy taxallusi bilan ijod qilgani taxmin qilinadi. Zebunniso begim hayoti va ijodi haqida Bareliy, Sherxoni Ludiy, Hakimxon to'ra kabi tarixchi va tazkiranavis olimlar asarlarida ma'lumotlar keltirilgan.`,
    `Buxoro shahri Xorazmshohlar davlatining yirik savdo va madaniy markazi bo'lgan. Shahar Mo'g'ullar imperiyasi bilan chegaradan ancha uzoqda joylashganligi sababli xorazmshoh bu shaharni himoya qilish uchun 20 000 dan kamroq askar ajratgan. Soni 30 000 dan 50 000 gacha bo'lgan mo'g'ul qo'shinlari katta qo'shin uchun o'tib bo'lmas to'siq deb hisoblangan Qizilqum cho'lini kesib o'tishga muvaffaq bo'lganlar.`,
    `Buxoro mudofaachilari butunlay sarosimaga tushgan va muvaffaqiyatsiz jangdan so'ng tashqi shahar uch kun ichida taslim bo'lgan. Xorazmlik hukmdorga sodiq askarlar qal'a buzib tashlangunga qadar ikki haftadan kamroq vaqt davomida mudofaani davom ettirganlar. Mo'g'ul qo'shini qo'rg'ondagi barchani o'ldirib, aholining katta qismini qul qilib olgan. `,
];
export const myuzbuch = [
    `Ta'lim tizimida savodxonlik darajasi 99,3 foizga yetdi, erkkaklar va ayollarning o'rtacha maktab yoshi 11 yoshni tashkil etdi. Biroq, Sovet Ittifoqi parchalanganidan keyin byudjet cheklovlari va boshqa o'tish davri muammolari tufayli darsliklar va boshqa maktab materiallari, o'qitish metodikasi, o'quv dasturlari va ta'lim muassasalari eskirgan, yetarli emas va e'tibordan chetda qolmoqda. Shuningdek, maktab yoshidagi odamlarning ulushi pasaymoqda. Hukumat bu masalalardan xavotirda bo'lsa-da, byudjet torligicha qolmoqda. Xuddi shunday, sog'liqni saqlash sohasida ham umr ko'rish davomiyligi uzoq edi, ammo Sovet Ittifoqi parchalanganidan keyin sog'liqni saqlash resurslari qisqardi, ularning sifati, foydalanish imkoniyati va samaradorligi pasaydi.`,
    `O'zbekiston Respublikasida juda katta ishlab chiqarish va mineral xom-ashyo boyligi, noyob qishloq xo'jaligi xom-ashyosi, qayta ishlash jarayonida olinadigan juda katta yarim fabrikatlar, boy tabiiy resurslar, ravnaq topgan infratuzilma bor.Foydali qazilmalar razvedkasining zamonaviy darajasi juda boy nodir, rangli va noyob metall konlarini, organik yonilg'ining barcha turlari - neft, tabiiy gaz, gaz kondensati, qo'ng'ir va yarim kokslanadigan ko'mir, yonuvchi slaneslar, uran, qurilish materiallari uchun xom ashyoning ko'p turlarini o'zlashtirish bilan bog'liqdir.O'zbekiston hududida 100 xildan ortiq mineral xom ashyolarning keng tarmog'i aniqlangan bo'lib, hozir ularning 60 dan ortig'i xalq xo'jaligining tarmoqlarida ishlatilmoqda.Oltin, uran, mis, tabiiy gaz, volfram, kaliy tuzlari, fosforit, kaolin singari foydali qazilmalarning tasdiqlangan zaxiralariga ko'ra O'zbekiston MDH emas, butun jahonda yetakchi o'rinni egallaydi.`,
    `Zebunniso begim - shoira, ma'rifatparvar. Boburiylar sulolasidan Avrangzebning qizi. Onasi Dilrasbonuning shajarasi Safaviylarga va Boburning qizi - Gulbadan begimga borib taqaladi. Maxfiy taxallusi bilan ijod qilgani taxmin qilinadi. Zebunniso begim hayoti va ijodi haqida Bareliy, Sherxoni Ludiy, Hakimxon to'ra kabi tarixchi va tazkiranavis olimlar asarlarida ma'lumotlar keltirilgan. U yetti yoshida Qur'onni to'liq yod olgan, keyinchalik falsafa, mantiq, yulduz ilmi, tarix, fiqh fanlarini yaxshi o'zlashtirgan, nasta'liq, nas'h, shikasta xatlarini mahorat bilan yozgan, bir necha marta husnixat bilan Qur'onni ko'chirgan. Alisher Navoiy, Zahiriddin Muhammad Bobur asarlarini o'rganib, she'riyatga bo'lgan ixlosi oshgan, atrofidagi shoir va olimlariga turli tarjimalar, qo'lyozmalarni ko'chirishni buyurtma qilgan, ularni maosh bilan ta'minlab kelgan. Zebunniso begimning kutubxonasi o'z davri kutubxonalari ichida eng kattasidir. Malika bobosining istagiga ko'ra o'z amakivachchasi Sulaymonshukuh bilan unashtirilgan. Amakivachchasi vafot etgach, umrining oxiriga qadar boshqa turmush qurmagan.`,
    `Acquacotta - italyan oshxonasida asosan issiq bulyon asosida tayyorlanadigan nonli sho'rva. U dastlab dehqonlarning taomi sifatida tayyorlangan. Italyanlar bu ovqatni tayyorlash va iste'mol qilishni qadim zamonlarda boshlagan. Bu taom ilk bora janubiy Toskana va Latsio shimolidagi Maremma deb nomlanuvchi qirg'oq hududida tayyorlanganligi aytiladi. Mazkur yegulik turi qotgan va eskirgan nonni iste'mol qilish uchun bir vosita sifatida o'ylab topilgan. Zamonaviy davrda taom uchun foydalaniladigan masalliqlar turi ko'payib, ba'zan qo'shimcha mahsulotlardan ham keng foydalanila boshlangan.Aslida Acquacotta qotgan nonni iste'mol qilish uchun bir vosita sifatida ixtiro qilingan`,
    `Tarixchi Pol Buellning ta'kidlashicha, shahar garnizoni emas, balki faqat Xorazmshohning yordamchi qo'shinlari amalga oshirgan jangovar harakat qochishga urinish bo'lishi mumkin. Tarixchi qo'shinlarning ketishga tayyorgarligi sababini Buxoro xorazmliklar qo'l ostiga o'tganiga ko'p bo'lmagani, ya'ni ushbu voqeadan o'n yil oldin Qoraxoniylar qo'lidan tortib olingani bilan izohlaydi. 10-fevralda shahar egallangan. Qal'a egallangach, yagona qarshilik markazi qal'adagi sadoqatli kuchlarning kichik guruhi bo'lib qolgan. Qal'a eng zamonaviy talablarga muvofiq qurilgan bo'lsa-da, Xon Xitoydan qamal urushi bo'yicha maxsus mutaxassislarni olib kelgan edi. O'n kundan keyin o't qo'yuvchi va poroxli qurollar qo'llanilganidan so'ng parokandalik sodir bo'lib, o'n ikkinchi kuni qarshilik to'xtatilgan va qal'a egallangan`,
    `Qal'aning biroz qarshilik ko'rsatishi Buxoroning qolgan qismiga zarar keltirdi. Mo'g'ullar qarshilik markazlarini bostirish uchun shaharga o't qo'yishgan. Shahardagi aksar inshootlar yog'och bo'lgani sababli, tez orada nazorat qilib bo'lmaydigan darajadagi yong'in shaharning ko'p qismini, shu jumladan mashhur kutubxonani ham kulga aylantirgan. Olovdan omon qolgan g'ishtin inshootlarning aksariyati, jumladan Poi Kalon ham mo'g'ullar tomonidan vayron qilingan. Faqat Minorai Kalon omon qolgan.Qo'rg'on ichida qatli om qilingan bo'lsa-da, Marv va Gurganj kabi boshqa shaharlardan farqli o'laroq, aholi butunlay yo'q qilinmagan. Qo'rg'on ichida qatli om qilingan bo'lsa-da, Marv va Gurganj kabi boshqa shaharlardan farqli o'laroq, aholi butunlay yo'q qilinmagan. `,
    `Bahorda daraxtlar g'uncha tugishga, havo asta-sekin isishni boshlaydi. Bu faslda qish faslida yog'ib o'tgan qorlarning erishi va juda ko'p miqdorda yomg'ir, do'l kabi yog'inlarning yog'ishi suv omborlarini, ko'l va ko'lmaklarning to'lishiga sabab bo'ladi. Chaqmoq va Momoqaldiroq kabi tabiat hodisalari ham eng ko'p bahor faslida ro'y beradi. Bahor faslida yog'inlarning va ba'zi tabiat hodisalarining ko'p miqdorda sodir bo'lishi ba'zi o'lkalarda toshqin, sel va yong'in kabi falokatlarga sabab bo'ladi.Bahorda daraxtlar g'uncha tugishga, havo asta-sekin isishni boshlaydi. Bu faslda qish faslida yog'ib o'tgan qorlarning erishi va juda ko'p miqdorda yomg'ir, do'l kabi yog'inlarning yog'ishi suv omborlarini, ko'l va ko'lmaklarning to'lishiga sabab bo'ladi. Chaqmoq va Momoqaldiroq kabi tabiat hodisalari ham eng ko'p bahor faslida ro'y beradi. Bahor faslida yog'inlarning va ba'zi tabiat hodisalarining ko'p miqdorda sodir bo'lishi ba'zi o'lkalarda toshqin, sel va yong'in kabi falokatlarga sabab bo'ladi.`,
    `Tabiat bizning hayotimizning ajralmas qismidir. U bizga nafaqat hayot uchun zarur bo'lgan resurslarni beradi, balki ruhiy va jismoniy salomatlikni ham ta'minlaydi. Ozbekistonda tabiiy go'zalliklar ko'plab joylarda uchraydi. Masalan, tog'lar, cho'llar va ko'llar bizning mamlakatimizning boy merosidir. Tog'larda sof havo va toza suv, cho'llarda esa o'ziga xos o'simliklar va hayvonlar dunyosi mavjud. Amudaryo va Sirdaryo kabi yirik daryolar esa mamlakatimizning asosiy suv manbalari hisoblanadi. Har bir inson tabiatni himoya qilishga o'z hissasini qo'shishi kerak, chunki u kelajak avlodlar uchun ham muhimdir.`,
    `Ilm-fan insoniyatning rivojlanishida muhim o'rin tutadi. U bizning hayotimizni osonlashtiradi va turli sohalarda yuksalishimizga imkon beradi. Ilmiy izlanishlar tufayli texnologiya, tibbiyot, muhandislik va boshqa ko'plab sohalarda katta yutuqlarga erishilgan. Masalan, kompyuter texnologiyalari va internet hayotimizning har bir sohasiga kirib keldi va kundalik faoliyatimizni samaraliroq qilmoqda. Tibbiyotda esa yangi davolash usullari va dori-darmonlar insonlarning umrini uzaytirish va sog'ligini yaxshilash imkonini berdi. Ilm-fan rivoji jamiyatning ma'naviy va madaniy taraqqiyotiga ham hissa qo'shadi. Yosh avlodni ilm-fan bilan shug'ullanishga rag'batlantirish va ta'limni rivojlantirish orqali kelajakda yanada katta muvaffaqiyatlarga erishishimiz mumkin.`,
    `Rivojlanish har bir jamiyatning asosiy maqsadlaridan biridir. Mamlakatning iqtisodiy, ijtimoiy va madaniy sohalarida yuksalish uning farovonligi va barqarorligi uchun muhimdir. Iqtisodiy rivojlanish yangi ish o'rinlari yaratish, turmush darajasini oshirish va texnologik yutuqlarni joriy etishni talab qiladi. Ijtimoiy rivojlanish esa ta'lim, sog'liqni saqlash va uy-joy sharoitlarini yaxshilash orqali amalga oshiriladi. Madaniy rivojlanish esa milliy qadriyatlarni saqlash va boyitishga qaratilgan. Har bir inson rivojlanishga hissa qo'shishi mumkin. Masalan, bilim olish, mehnat qilish va jamiyatning faol a'zosi bo'lish orqali. Rivojlanishning barqaror bo'lishi uchun tabiatni asrash va resurslarni oqilona foydalanish ham muhimdir. Shunday qilib, har tomonlama rivojlanish bizning kelajagimizni yanada yorqin va barqaror qiladi.`,
];
export const myuzbbesh = [
    `O'zbekiston iqtisodiyoti bozor iqtisodiyotiga bosqichma-bosqich o'tadi, tashqi savdo siyosati import o'rnini bosishga asoslangan. 2017-yil sentabridan boshlab mamlakat valyutasi bozor kursi bo'yicha to'liq konvertatsiya qilinmoqda. O'zbekiston paxta tolasini ishlab chiqaruvchi va eksport qiluvchi yirik ta'minotchi davlatdir. Mamlakatda, shuningdek, dunyodagi eng yirik oltin konlari mavjud. Sovet davridagi ulkan energiya ishlab chiqarish qurilmalari va tabiiy gazni yetkazib berish bilan O'zbekiston Markaziy Osiyodagi eng yirik elektr ishlab chiqaruvchisi bo'ldi.O'zbek etnonimining kelib chiqishi to'g'risida bir nechta tahminlar mavjud. Shaxsiy ism sifatida o'zbek so'zining birinchi eslatilishi XII asrga to'g'ri keladi. O'zbek so'zi turkiy qabilalari orasida Movarounnahrda XIII asrda mo'g'ullar kelguniga qadar paydo bo'lgan.`,
    `Bugungi kunda O'zbekiston nomi bilan tanilgan hudud oldin Movarounnahr, So'g'diyona hamda Buxoro xonligi nomlari bilan ham tanilgan. XIV asrda ushbu mintaqada Amir Temur dunyoga kelgan va o'z saltanatining markaziy qismiga aylantirgan. Amir Temur davrida ushbu hudud Qora dengizdan Arabiston dengiziga va Hindistonning Dehli shahriga qadar cho'zilgan imperiyaning poytaxtiga aylangan.Markaziy Osiyoda birinchi bo'lib o'rnashgan odamlar Skiflar bo'lib, ular bugungi O'zbekiston hududining shimolida joylashgan dashtlardan tahminan miloddan avvalgi birinchi mingyillikda ko'chib kelishgan. Ushbu ko'chmanchilar hududga kelib, daryolar vodiylarida keng qamrovli sug'orish tizimlarini barpo etishgan.`,
    `Sharqiy Osiyo va G'arb orasida savdo aloqalari rivojlanar ekan, Movarounnahr hududidagi shaharlar va qishloqlar ushbu savdo aloqalarida muhim ahamiyat kasb eta boshladi va ushbu savdo munosabatlarida badavlat so'g'd savdogarlarining roli katta bo'lgan. Keyinchalik Buyuk Ipak yo'li nomi bilan tanilgan ushbu savdo yo'lida joylashgan Buxoro va Samarqand kabi shaharlar oxir-oqibatda o'ta badavlat shaharlarga aylandi va Movarounnahrning o'zi esa Antik davrning eng ta'sirli va qudratli hududlaridan biriga aylandi.`,
    `Chingizxonning 1227-yilda vafot etishi bilan Mo'g'ullar saltanati uning to'rt o'g'illari va qarindoshlari orasida bo'lib olindi. Saltanatning parchalanib ketish xavfi mavjud bo'lsada, bir necha avlod davomida hukmdorlar tartib va an'anaga ko'ra almashdi va Movarounnahr ustidan nazorat Chingizxonning ikkinchi o'g'li Chig'atoy xonning bevosita zurriyodlari va avlodlari qo'lida qoldi. Chig'atoy xonligi davridagi hukmdorlarning tartibli almashishi, ichki osoyishtalik va farovonlik Mo'g'ullar saltanatining ham kuchli va uyushgan davlat sifatida saqlanib qolishiga sabab bo'ldi.`,
    `Somoniylar davlatining eng mashhur hukmdori Ismoil Somoniy bo'lgan. U butun Movarounnahrni o'z qo'li ostida kuchli bir davlat qilib birlashtirdi. Xurosonda tashkil topgan Safforiylar davlatiga barham berdi va uni o'z davlatiga qo'shib oldi. Natijada poytaxti Buxoro shahri bo'lgan zamonasining eng yirik mustaqil davlati tashkil topdi. Ismoil Somoniy 907 yilgacha hukmronlik qildi.Movarounnahr va Xurosondagi o'rta asr davlati. Somoniylar davlatining tashkil topishi Arab xalifaligining qulashi hamda Movarounnahr va Xurosonni boshqargan Somoniylarning davlat tepasiga chiqishi bilan bog'liq. Ravnaq topgan davrida Movarounnahr, Xuroson, Shimoliy va Sharqiy Eronni o'z ichiga olgan.`,
    `Qoraxoniylardavrida turli o'lkalarning birlashishi fan va madaniyat rivojiga ham imkon berdi. Bu davrda Buxoro, Samarqand, Bolasog'un, Qashqar, O'zgan kabi shaharlar ham ma'muriy, ham madaniy markaz sifatida ahamiyati oshib, yanada kengayib, rivojlanib borgan. Qoraxoniylar fan va madaniyatning o'lka hayotidagi muhim o'rnini anglab unga homiylik qilishgan. Buyuk ipak yo'li asosidagi hududlararo savdo-sotiq yanada ravnaq topdi.Movarounnahr va Xorazmda hozirgi o'zbeklarga xos turkiy etnos qaror topdi va mazkur davrda o'zbek xalqiga xos elatni belgilovchi hudud, til, madaniyat, tarixiy qismatining umumiyligi, etnik o'zlikni anglash, etnosning uyushqoqligi ma'lum bir davlat doirasida bo'lishi, din umumiyligi va bir qancha shu kabi boshqa etnik alomatlar shakllangan.`,
    `1077-yilda Malikshoh Anushtegin Garchoini Xorazmga noib etib tayinlaydi va Xorazmda Anushteginiylar sulolasi hukmronligi boshlanadi. Anushteginlar turkiylar sulolasidan bo'lib, o'sha paytda, Osiyoda hukmronlik qilgan uch buyuk sulola: Anushteginlar, Saljuqiylar va G'aznaviylar bir negizdan kelib chiqqan. Qutbiddin Muhammad Xorazmshoh unvonini tiklab, shu nom bilan ulug'lansa ham Saljuqiylarning sadoqatli noibi bo'lib qoldi. Qayta tiklangan xoramshoh mansabiga sazovor bo'lgan Anushtegin nabirasi Otsiz Xorazm mustaqilligi yo'lida astoydil harakat qilgan. Xorazmshoh Otsiz saljuq sultoni Sanjarga qarshi muvaffaqiyatli kurash olib borgan.`,
    `Yangi texnikalar hayotimizda muhim o'rin egallayapti va dunyoni o'zgartiradi. Texnologiyalar yangilanishi bilan birga, biz osonlik va samaraliqda ishlashimizga imkon beradi. Kiber-xavfsizlik sohasidagi yangiliklar, masalan, shaxsiy ma'lumotlarni himoya qilishning yangi usullari, onlayn xaritadan foydalanish, va biometrik identifikatsiya tizimlari, onlayn xaritalar bilan sayohatlarimizni rejalashtirishni osonlashtiradi va xavfsiz qiladi. Sun'iy intellekt va mexanik o'qitish sistemalari texnologik inobatga olinayotgan sohalarning biridir. Bu sistemalar, bizning amaliyotlarimizni tahlil qilish va natijalarga asoslangan tavsiyalar berish orqali, ish jarayonlarimizni yaxshilaydi va yengilab turadi. 3D-printerlar esa yaratish sohasida revolyutsion darajada o'zgartirish keltirib chiqdi. 3D-printerlar orqali, biz qulaylik bilan mahsulotlar, qurilmalar va istalgan narsani yarata olamiz. Bular qiziqarli yangiliklar bilan birga, texnologiyalar hayotimizning har bir sohasini rivojlantiradi va yaxshilaydi.`,
    `Sun'iy intellekt (SI) - bu kompyuter tizimlari va dasturlari orqali shaxslarning o'zining o'zini tahlil qilishi, o'rganishi va maqbul xulosa chiqarishi mumkin bo'lgan yuqori darajada miqyosli odam haqida bilish va boshqarish qobiliyatlari. SI ko'p yo'l harakatda olib borilayotgan va har kuni bizning hayotimizga katta ta'sir ko'rsatayotgan texnologik inobatga olingan soha hisoblanadi.Sun'iy intellektning ilmiy, iqtisodiy, tibbiy va boshqa sohalarda ko'plab foydalari mavjud. Masalan, tibbiyotda, SI rivojlanayotgan xavfsizlik va natijalarga o'zining tibbiy tarqatuvlarini ishlab chiqish, tahlil qilish va shifokorlarga dori-darmonlarni tavsiya qilishda yordam beradi. Iqtisodiy sohada, SI o'zining inflyatsion, ehtimollik va boshqa analitik vositalar orqali bozorlar va investitsiyalar haqida aniqlashlar qilishda yordam beradi.`,
    `Boshqa joyda, SI o'zining yuqori hisoblash va ta'lim qobiliyatlari orqali turli sohalarda mashg'ulotni avtomatlashtirish va yaxshilashda muhimoq rol o'ynaydi. Masalan, siyosiy tashkilotlar SI orqali ma'lumotlar analizini olib borish orqali qarorlarini qabul qilishadi.Bunday yuqori tekislikli tizimlar va dasturlar bilan, SI hayotimizning har bir sohasini rivojlantiradi va yaxshilaydi. Buning natijasida, yangi imkoniyatlar ochiladi va odamlar o'zlarining vazifalarini bajarishda yordam beradigan yangi usullarni topishadi.`,
];
